/* Style */
body {
  font-size: 15px;
  font-family: Arial, Arial, Helvetica, sans-serif;
  line-height: 1.5;
  background: #D1D1D1;
}

a {
  color: #63a52a;
  text-decoration: none;
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

a:hover {
  text-decoration: underline;
  color: #90D355;
}

h1.title {
  margin: 30px 20px 10px;
  font-size: 60px;
  font-weight: bold;
  font-style: italic;
  font-family: Georgia, serif;
  text-align: center;
}

.wrapper {
  width: 675px;
  margin: 0 auto;
}

#container {
  border: 1px solid #2a2a2a;
  background: #ddd url(../images/pattern.png);
  box-shadow: 0 0 5px #b1b1b1;
}

p.tagline {
  padding: 20px 20px 0;
  color: #fff;
  font-size: 17px;
}

#main {
  margin-top: 20px;
  padding: 0 20px 90px;
  background-color: #fff;
}

.download-bar {
  background: #222;
  border: 5px solid #444;
  padding: 10px;
  margin: 0 -35px 20px;
  position: relative;
}

.download-bar .inner {
  overflow: hidden;
}

.download-bar .watch-fork iframe {
  display: block;
  float: left;
  border-right: 1px solid #ddd;
  padding-right: 5px;
}

.download-bar .watch-fork iframe.last {
  border-right: 0 none;
  padding-right: 0;
  padding-left: 5px;
  border-left: 1px solid #fff;
}

.download-bar .watch-fork {
  overflow: hidden;
  float: right;
  background-color: #eee;
  padding: 5px;
  border-radius: 3px;
}

.download-bar .blc {
  border: 10px solid black;
  border-color: transparent transparent black;
  width: 0;
  height: 0;
  display: block;
  position: absolute;
  bottom: -15px;
  left: 0;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  /* IE9 */
  -webkit-transform: rotate(45deg);
  /* 2014 current */
}

.download-bar .trc {
  border: 10px solid black;
  border-color: black transparent transparent;
  width: 0;
  height: 0;
  display: block;
  position: absolute;
  top: -15px;
  right: 0;
  transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  /* IE9 */
  -webkit-transform: rotate(45deg);
  /* 2014 current */
}

.download-bar .avatar {
  border: 1px solid black;
  display: block;
  padding: 4px;
  float: left;
}

.download-bar .avatar img {
  display: block;
}

.download-bar a.code {
  background: transparent url(../images/code.png) no-repeat 0 2px;
  padding-left: 35px;
  margin-top: 8px;
  display: block;
  float: left;
  text-indent: 0;
  width: auto;
  height: auto;
  opacity: 1;
  filter: alpha(opacity=100);
  /* IE 5-7 */
}

.current-section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 693px;
  margin-left: -352px;
  background: #222;
  border: 5px solid #444;
  color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

.current-section p {
  padding: 5px 27px;
  font-size: 24px;
  font-weight: bold;
}

.current-section a {
  float: right;
  text-indent: -10000px;
  background: transparent url(../images/top.png) no-repeat 0 0;
  width: 20px;
  height: 20px;
  opacity: 0.8;
  margin-right: 12px;
  margin-top: 12px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

.current-section a:hover {
  opacity: 1;
  filter: alpha(opacity=100);
  /* IE 5-7 */
}

.current-section a.zip {
  margin-right: 8px;
}

a.zip,
a.zip span {
  background: transparent url(../images/zip.png) no-repeat 0 0;
  width: 30px;
  height: 21px;
  display: inline-block;
  text-indent: -10000px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

a.tar,
a.tar span {
  background: transparent url(../images/tar.png) no-repeat 0 0;
  width: 30px;
  height: 21px;
  display: inline-block;
  text-indent: -10000px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

a.code {
  background: transparent url(../images/code.png) no-repeat 0 2px;
  width: 30px;
  height: 21px;
  display: block;
  display: inline-block;
  text-indent: -10000px;
  opacity: 0.8;
  filter: alpha(opacity=80);
  /* IE 5-7 */
  transition: opacity ease-in-out 0.3s;
  -webkit-transition: opacity ease-in-out 0.3s;
  /* Safari <=6.1, Android <= 4.3 */
}

a.zip:hover,
a.tar:hover,
a.code:hover {
  opacity: 1;
  filter: alpha(opacity=100);
}

a.download-button {
  border: 1px solid black;
  border-radius: 3px;
  display: inline-block;
  text-indent: 0 !important;
  width: auto;
  float: right;
  background: #999;
  /* for non-css3 browsers */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#37ADD4', endColorstr='#1B657E');
  /* IE <= 9 */
  background: -webkit-gradient(linear, left top, left bottom, from(#37ADD4), to(#1B657E));
  /* ancient webkit browsers */
  background: -webkit-linear-gradient(top, #37ADD4, #1B657E);
  /* Safari <=6.1, Android <= 4.3 */
  background: linear-gradient(to bottom, #37ADD4, #1B657E);
  height: auto;
  margin-left: 10px;
}

a.download-button span {
  background-position: 10px 5px;
  width: auto;
  height: auto;
  padding: 5px 10px;
  padding-left: 45px;
  display: inline-block;
  text-indent: 0 !important;
  color: #fff;
}

footer {
  margin-bottom: 60px;
  padding-bottom: 60px;
}

footer .owner {
  background: #222;
  border: 5px solid #444;
  padding: 5px 15px;
  margin: -67px -10px 35px;
  color: #d6d6d6;
}

footer .creds small {
  float: right;
  font-size: 10px;
  text-align: right;
  margin-left: 15px;
}

footer .owner .avatar {
  background-color: #666;
  display: block;
  margin: -19px 10px 0 0;
  width: 60px;
  float: left;
}

footer .owner img {
  display: block;
  border: 1px solid #2a2a2a;
  margin: 5px;
}

footer .owner p {
  font-family: Georgia, serif;
}

footer .owner p a {
  font-size: 16px;
  font-style: italic;
}

/* Markdown */
.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6,
.markdown-body p,
.markdown-body pre,
.markdown-body ul,
.markdown-body ol,
.markdown-body dl,
.markdown-body table,
.markdown-body blockquote {
  margin-bottom: 20px;
}

.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  font-weight: bold;
}

.markdown-body h1 {
  font-size: 28px;
}

.markdown-body h2 {
  font-size: 24px;
  color: #557398;
}

.markdown-body h3 {
  font-size: 20px;
}

.markdown-body h4 {
  font-size: 18px;
}

.markdown-body h5 {
  font-size: 16px;
}

.markdown-body pre {
  padding: 10px 70px 10px 0;
  margin-left: -20px;
  margin-right: -20px;
  font-family: 'Monaco', 'Lucida Console', monospace;
  font-size: 13px;
  line-height: 20px;
  box-shadow: inset 0 0 5px #000;
  word-wrap: break-word;
  background-color: #3b3b3b;
  color: #d6d6d6;
}

.markdown-body pre.lines {
  font-size: 12px;
  margin: 0 10px 0 -20px;
  padding: 10px;
  float: left;
  display: block;
  text-align: right;
  box-shadow: none;
  background-color: #2a2a2a;
  color: #d6d6d6;
}

.markdown-body ul,
.markdown-body ol {
  padding-left: 30px;
}

.markdown-body ul {
  list-style-type: disc;
}

.markdown-body ol {
  list-style-type: decimal;
}

.markdown-body li,
.markdown-body li p,
.markdown-body dd,
.markdown-body dd p {
  margin-bottom: 10px;
}

.markdown-body li pre,
.markdown-body li pre.lines,
.markdown-body dd pre,
.markdown-body dd pre.lines {
  margin-left: -35px;
}

.markdown-body dt {
  font-weight: bold;
  font-style: italic;
}

.markdown-body dd {
  margin-left: 15px;
}

.markdown-body table {
  width: 673px;
  margin-left: -20px;
  margin-right: -20px;
}

.markdown-body tbody {
  border-top: 2px solid #557398;
  border-bottom: 2px solid #557398;
  background-color: #EBEFF4;
}

.markdown-body table td * {
  margin: 0;
}

.markdown-body td {
  border-right: 1px solid #557398;
  border-bottom: 1px solid #557398;
  padding: 5px;
}

.markdown-body td:first-child,
.markdown-body th:first-child {
  width: 30%;
  padding-left: 20px;
}

.markdown-body td:last-child {
  border-right: 0 none;
}

.markdown-body th {
  font-size: 18px;
  font-weight: bold;
  text-align: left;
  padding: 5px;
}

.markdown-body tt {
  background-color: #3b3b3b;
  color: #d6d6d6;
  padding: 2px 3px;
}

.markdown-body blockquote {
  font-style: italic;
  font-family: Georgia, serif;
  font-size: 17px;
  border-top: 3px solid #333;
  border-bottom: 3px solid #333;
  padding: 10px 20px;
  padding-left: 50px;
}

.markdown-body blockquote:before {
  font-style: italic;
  font-family: Georgia, serif;
  font-size: 90px;
  height: 90px;
  margin-left: -60px;
  margin-top: -25px;
  content: "‟";
  display: block;
  float: left;
}

.markdown-body img {
  max-width: 100%;
  box-sizing: border-box;
}

