.grid {
  box-sizing: border-box;
  list-style: none;
  margin: 0;
  padding: 0;
  min-width: 100%;
  border: 0 solid #000;
  display: flex;
  align-content: stretch;
}

.grid__wrapper {
  overflow: hidden;
}

.grid__cell {
  box-sizing: border-box;
  min-width: 0%;
  max-width: 100%;
  display: block;
  padding: 0;
  margin: 0;
  background-clip: padding-box !important;
  border: 0 solid transparent;
  flex-grow: 1;
  flex-shrink: 1;
}

.grid__cell--vAlign-center {
  align-self: center;
}

.grid__cell--vAlign-top {
