  flex-shrink: 1;
}

.grid__cell--noGrowth {
  flex-grow: 0;
}

.grid__cell--noShrink {
  flex-shrink: 0;
}

.grid__cell--noResize {
  flex-shrink: 0;
  flex-grow: 0;
}

.grid__inner {
  display: block;
}

